
.footer {
  background-color: #111; 
  color: #fff;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
}

.container3 {
  max-width: 1200px;
  margin: auto;
}


.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
}


.cardlast {
  flex: 1 1 300px;
  min-width: 250px;
}


.cardlast p {
  margin: 10px 0;
  font-size: 14px;
  line-height: 1.5;
}


.cardlast i {
  margin-right: 8px;
  color: #ff9800;
}


.cardlast form {
  display: flex;
  flex-direction: column;
}

.cardlast input[type="text"],
.cardlast input[type="password"],
.cardlast input[type="email"] {
  padding: 10px;
  margin: 8px 0;
  border: none;
  border-radius: 5px;
}

.cardlast input[type="submit"] {
  background-color: #ff9800;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.cardlast input[type="submit"]:hover {
  background-color: #e68900;
}


.icon {
  margin-top: 10px;
  align-items: center;
}

.icon a {
  color: white;
  margin-right: 10px;
  font-size: 20px;
  transition: 0.3s;
}

.icon a:hover {
  color: #ff9800;
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    text-align: center;
  }
  .icon {
    justify-content: center;
  }
}
