.login {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.login-container {
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  width: 350px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-in-out;
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
}

.login-title h2 {
  font-size: 24px;
  color: #333;
}

.login-inputs {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-inputs input {
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 14px;
}

.login-inputs input:focus {
  border-color: #ff6600;
}

button {
  width: 100%;
  padding: 12px;
  background-color: #ff6600;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 16px;
  font-weight: bold;
  margin-top: 2px;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background-color: #e65500;
}

.login-condition {
  display: flex;
  align-items: center;
  font-size: 12px;
  margin-top: 10px;
  color: #555;
}

.login-condition input {
  margin-right: 8px;
}

p {
  font-size: 14px;
  margin-top: 15px;
  text-align: center;
}

p span {
  color: #ff6600;
  cursor: pointer;
  font-weight: bold;
}

p span:hover {
  text-decoration: underline;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
