body{
  background-color: black;
 
}

.container2 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  padding: 25px;
  background: black;
   
 
}

.card {
  background: grey;
  border-radius: 12px;
  box-shadow: 0 4px 10px green;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.card img {
  max-width: 100%;
  height: 180px;
  object-fit: fill;
  border-radius: 8px;
}

.card p {
  margin: 8px 0;
  font-size: 14px;
}

.card p:first-of-type {
  font-weight: bold;
  font-size: 16px;
}

.card button {
  margin-top: auto;
  background: rgba(29, 112, 90, 0.336);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  
}

.card button:hover {
  background: black;
}
